.experienceList {
  width: 85%;
  margin: auto;
  display: flex;
  // justify-content: center;

  .experience {
    ul > li {
      position: relative;
      padding: 0 20px 0 60px;
      margin: 0 0 50px;
    }

    ul > li > .icon {
      position: absolute;
      left: 0px;
      right: 0px;
      z-index: 1;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      text-align: center;
      line-height: 40px;
      background-color: #000;
    }

    ul > li > .time {
      display: inline-block;
      padding: 1px 10px;
      margin-bottom: 12px;
      border-radius: 20px;
      background-color: rgb(37, 37, 37);
      font-size: 12px;
      font-weight: 600;
      letter-spacing: 0.6px;
      opacity: 0.8;
      color: #fff;
    }

    ul > li > h4 {
      margin: 7px 0px 10px;
      font-size: 18px;
      font-weight: 500;
      opacity: 0.8;
    }

    ul > li > h4 span {
      position: relative;
      padding-left: 26px;
      font-size: 15px;
      font-weight: 600;
      opacity: 0.6;
    }

    ul > li > h4 span:before {
      position: absolute;
      content: "";
      width: 10px;
      height: 2px;
      background-color: rgb(255, 255, 255);
      left: 7px;
      top: 9px;
      opacity: 0.8;
    }

    ul > li::after {
      content: "";
      position: absolute;
      top: 0px;
      left: 20px;
      bottom: 0px;
      border-left: 1px solid rgb(51, 51, 51);
    }
  }
}

.about-cont {
  padding: 0 30px;
}

.aboutTitle {
  margin-bottom: 40px;
  font-size: 1.625rem;
  text-align: center;
  font-family: "Chosunilbo_myungjo";
}

.skillCont {
  padding: 0 30px;
  display: flex;
  flex-wrap: wrap;
}

.skill-list {
  width: calc(50% - 30px);
  margin: 0px 15px 30px;
  padding: 20px 30px;
  border: 1px solid rgb(37, 37, 37);
  border-radius: 5px;
  cursor: default;
  position: relative;
}

.skill-list::before {
  content: "";
  box-sizing: inherit;
  position: absolute;
  border: 1px solid transparent;
  width: 0px;
  height: 0px;
  border-radius: 5px;
}

.skill-list .skill-label {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  svg {
    margin-right: 10px;
  }
}

.skill-list .skill-detail li {
  position: relative;
  padding: 0px 0px 10px 20px;
  font-size: 14px;
  line-height: 1.5;
  color: #000;
}

.skill-list .skill-detail li::before {
  content: "";
  position: absolute;
  left: 0px;
  top: 13px;
  width: 10px;
  height: 1px;
  background: #000;
}

@media screen and (max-width: 640px) {
  .skill-list {
    width: 100%;
  }
}
